<template>
  <div class="container" id="login">
    
    <div class="form-login">
      <div class="login-header">
        <div class="login-menu">
          <button
            :class="activeForm"
            @click="
              displayForm = 'display:block;'
                ? (displayQr = 'display:none;',displayForm = 'display:block;',activeForm = 'active',activeqr = '')
                : (displayForm = 'display:block; ')
            "
            title="connect with mail"
          >
            <v-icon icon="mdi-email" class="icon"></v-icon></button
          ><button
            @click="
              displayQr = 'display:block;'
                ? (displayForm = 'display:none;',displayQr = 'display:block;',activeForm = '',activeqr = 'active')
                : (displayQr = 'display:block; ')
            "
            :class="activeqr"
            title="connect with qr code"
          >
            <v-icon icon="mdi-qrcode" class="icon"></v-icon>
          </button>
        </div>
      </div>
      <div class="form lg" :style="displayForm">
        <form>
          <div class="form-group">
            <label for="exampleInputEmail1">Email address</label>
            <input
              type="email"
              class="form-control"
              id="exampleInputEmail1"
              aria-describedby="emailHelp"
              placeholder="Enter email"
            />
          </div>
          <div class="form-group">
            <label for="exampleInputPassword1">Password</label>
            <input
              type="password"
              class="form-control"
              id="exampleInputPassword1"
              placeholder="Password"
            />
          </div>
          <button type="submit" class="btn">Connect</button>
        </form>
      </div>
      <div class="qrcode lg" :style="displayQr">
        <h1>Qr Code</h1>
      </div>
    </div>
  </div>
</template>

<script>
import logo from "@/assets/img/logo.png";
export default {
  name: "LoginView",

  data() {
    return {
      logo: logo,
      displayQr: "display:none;",
      displayForm: "display:block;",
      activeForm:'active',
      activeqr: ''
    };
  },
};
</script>

<style scoped>
.active {
    color: #fed638 !important;
}
#login {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 90vh;
}
.form-login {
  display: flex;
  flex-direction: column;
  gap: 20px;
  min-width: 200px;
    width: 100%;
    max-width: 350px;
}
.login-menu {
  display: flex;
  align-items: center;
  justify-content: space-around;
  background-color: white;
}
.login-menu button {
    color: black;
    font-size: 20px;
}

.lg {
    min-height: 200px;
}
.qrcode {
    display: flex;
    align-items: center;
    justify-content: center;
}
.btn {
    width: 100%;
    margin: 15px 0;
    background-color: #fed638;
}
.btn:hover {
    background-color: #fed638;
}
</style>
