<template>
  <div class="container" id="nav">
    <div class="logo">
      <img :src="logo" />
      <h1 style="display: none" id="logo">
        <span style="color: white">ISMAIL</span> DEKKAN
      </h1>
    </div>
    <div class="items navigation" v-show="!mobile">
      <div class="navlinks">
        <router-link to="/" active-class="active-link">Home</router-link>
        <router-link to="/about" active-class="active-link">About</router-link>
        <router-link to="/resume" active-class="active-link"
          >Resume</router-link
        >
        <router-link to="/projects" active-class="active-link"
          >Projects</router-link
        >
        <router-link to="/contact-me" active-class="active-link"
          >Contact</router-link
        >
      </div>
    </div>
    <div class="icon">
      <v-icon
        class="actual-icon"
        color="#FFF0DB"
        @click="toggleMobileNav"
        v-show="mobile"
        size="24px"
        :class="{ 'icon-active': mobileNav }"
      >
        {{ icon }}
      </v-icon>
    </div>
    <transition name="slide-fade">
      <div class="dropdown-nav items" v-show="mobileNav">
        <router-link
          to="/"
          active-class="active-link"
          @click="(mobileNav = false), (icon = 'mdi-menu')"
          >Home</router-link
        >
        <router-link
          to="/about"
          active-class="active-link"
          @click="(mobileNav = false), (icon = 'mdi-menu')"
          >About</router-link
        >
        <router-link
          to="/resume"
          active-class="active-link"
          @click="(mobileNav = false), (icon = 'mdi-menu')"
          >Resume</router-link
        >
        <router-link
          to="/projects"
          active-class="active-link"
          @click="(mobileNav = false), (icon = 'mdi-menu')"
          >Projects</router-link
        >
        <router-link
          to="/contact-me"
          active-class="active-link"
          @click="(mobileNav = false), (icon = 'mdi-menu')"
          >Contact</router-link
        >
      </div>
    </transition>
  </div>
  <router-view class="anim" />
</template>
<script>
import logo from "@/assets/img/logo.png";

export default {
  data: () => {
    return {
      logo: logo,
      defaultIcon: "mdi-white-balance-sunny",
      mobile: null,
      mobileNav: null,
      icon: "mdi-menu",
    };
  },

  methods: {
    checkScreen() {
      this.windowWidth = window.innerWidth;
      if (this.windowWidth <= 650) {
        this.mobile = true;
      } else {
        this.mobile = false;
        this.mobileNav = false;
        this.icon = "mdi-menu";
      }
    },
    toggleMobileNav() {
      this.mobileNav = !this.mobileNav;
      this.mobileNav ? (this.icon = "mdi-close") : (this.icon = "mdi-menu");
    },
  },
  mounted() {},
  created() {
    window.addEventListener("resize", this.checkScreen);
    this.checkScreen();
  },
  computed() {},
};
</script>
